import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: () => import("../Views/HomePage.vue"),
  },
  {
    path: "/contact-me",
    name: "Contact Me",
    component: () => import("../Views/ContactMe.vue"),
  },
  {
    path: "/portfolio",
    name: "Portfolio",
    component: () => import("../Views/MyPortfolio.vue"),
  },
  {
    path: "/collaborations",
    name: "Collaborations",
    component: () => import("../Views/MyCollaborations.vue"),
  },
  {
    path: "/private/access",
    name: "unnamed-route",
    component: () => import("../components/PrivateAccess/PrivateAccess.vue"),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
