<template>
  <header>
    <v-app-bar elevation="4" app>
      <v-app-bar-nav-icon @click="drawer = true"></v-app-bar-nav-icon>
      <v-spacer></v-spacer>
      <img
        :src="require('@/assets/images/logo.png')"
        alt=""
        @click="$router.push('/')"
        style="cursor: pointer"
      />
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" app temporary>
      <v-list nav>
        <v-list-item-group v-model="group">
          <v-list-item>
            <v-list-item-title @click="$router.push('/'), (drawer = false)"
              >Home</v-list-item-title
            >
          </v-list-item>
          <v-list-item>
            <router-link to="/portfolio">
              <v-list-item-title>Portfolio / Creations</v-list-item-title>
            </router-link>
          </v-list-item>
          <v-list-item>
            <router-link to="/collaborations">
              <v-list-item-title>Collaborations</v-list-item-title>
            </router-link>
          </v-list-item>
          <v-list-item>
            <v-list-item-title @click="scrollToAboutMe()"
              >About Me</v-list-item-title
            >
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </header>
</template>

<script>
export default {
  data() {
    return {
      drawer: false,
      group: null,
    };
  },
  methods: {
    scrollToAboutMe() {
      this.drawer = false;
      if (this.$route.path !== "/") {
        this.$router.push("/");
        setTimeout(() => {
          document.getElementById("aboutMeSection").scrollIntoView({
            behavior: "smooth",
          });
        }, 300);
      } else {
        document.getElementById("aboutMeSection").scrollIntoView({
          behavior: "smooth",
        });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
* {
  font-family: monospace;
  font-weight: 500;
  letter-spacing: 1.5px;
  a {
    text-decoration: none;
    color: black !important;
  }
}
</style>
